.burger {
  position: absolute;
  top: 16px;
  right: 50px;
  z-index: 102;
  display: none;
}

.burger:hover {
  cursor: pointer;
}

.burger-part {
  width: 36px;
  height: 4px;
  margin-top: 8px;
  border-radius: 15px;
  background: black;
  transition: 0.3s ease;
}

.burger-opened {
  transform: 0.4s ease;
  z-index: 101;
  position: fixed;
}

.burger-opened .burger-part {
  background: white;
}

.burger-part:first-of-type {
  margin-top: 0;
}

.burger-opened .burger-part:first-of-type {
  transform: translateY(12px) rotate(45deg);
}

.burger-opened .burger-part:nth-child(2) {
  display: none;
}

.burger-opened .burger-part:nth-child(3) {
  transform: rotate(-45deg);
}

